<template>
  <div class="data-table">
    <a-table :rowSelection="rowSelection"
             :columns="obj.columns"
             :pagination="obj.pagination"
             :loading="obj.loading"
             :rowKey="record => record.pid"
             :dataSource="obj.tabdata"
             :customRow="(res) => {
               return {
                 on: {
                   //click: (e) => {
                   //  this.rowHandler(e,res)
                   //}
                 }
               }
             }"
             @change="handleTableChange">
      <div slot="status"
           slot-scope="text, record, index"
           @click.stop="bubbling">
        <a-switch checkedChildren="可采集"
                  unCheckedChildren="未采集"
                  :defaultChecked="text.status == '1'"
                  @change="switchBtn(text, record, index)" />
      </div>
      <div slot="siteLink"
           slot-scope="text, record, index"
           @click.stop="bubbling">
        <a :href="text.address"
           target="_blank">{{text.address}}</a>
        <!-- <a-switch checkedChildren="可采集"
                  unCheckedChildren="未采集"
                  :defaultChecked="text.status == '1'"
                  @change="switchBtn(text, record, index)" /> -->
      </div>
      <div slot="remark"
           slot-scope="text, record, index"
           @click.stop="bubbling">
        <textarea name=""
                  id=""
                  :value="text.remark"
                  @keyup.enter="remarkHandler(text)"></textarea>
        <!-- <input :value="text.remark" type="text"
               @keyup.enter="remarkHandler(text)"> -->
      </div>
      <div slot="rolu"
           slot-scope="text, record, index"
           @click.stop="bubbling">
        <a-button type="primary"
                  size="small"
                  @click="configuration(text)">
          配置信息
        </a-button>
        <!-- <input :value="text.remark" type="text"
               @keyup.enter="remarkHandler(text)"> -->
      </div>
      <div slot="detection"
           slot-scope="text, record, index"
           @click.stop="bubbling">
        <a-button type="primary"
                  size="small"
                  @click="monitor(text)">
          监测
        </a-button>
        <!-- <input :value="text.remark" type="text"
               @keyup.enter="remarkHandler(text)"> -->
      </div>
    </a-table>
    <a-modal title="信息配置 "
             centered
             okText="确认"
             cancelText="取消"
             v-model="conInfo"
             @ok="conCommit">
      <a-form :form="form">
        <a-form-item v-bind="formItemLayout"
                     label="过滤规则">
          <a-input v-decorator="['filter_rule']" />
        </a-form-item>
        <a-form-item v-bind="formItemLayout"
                     label="自定义采集页数">
          <a-input v-decorator="['page_size']"
                   placeholder="0表示不限制采集页数" />
        </a-form-item>
        <a-form-item v-bind="formItemLayout"
                     label="标题">
          <a-input v-decorator="['header']" />
        </a-form-item>
        <a-form-item v-bind="formItemLayout"
                     label="作者">
          <a-input v-decorator="['author']" />
        </a-form-item>
        <a-form-item v-bind="formItemLayout"
                     label="发布时间">
          <a-input v-decorator="['issueTime']" />
        </a-form-item>
        <a-form-item v-bind="formItemLayout"
                     label="内容">
          <a-textarea v-decorator="['content']"></a-textarea>
        </a-form-item>
      </a-form>
    </a-modal>
  </div>
</template>

<script>
export default {
  name: 'Table',
  props: ['obj'],
  data () {
    return {
      pagination: { current: 1 },
      switchState: null,
      status: 0,
      pid: null,
      conInfo: false,
      formItemLayout: {
        labelCol: {
          xs: { span: 6 }
        },
        wrapperCol: {
          xs: { span: 24 },
          sm: { span: 16 }
        }
      },
      form: this.$form.createForm(this),
      fieldsValue: '{}'
    }
  },
  mounted () {
  },
  methods: {
    rowHandler (e, res) {
      let pid = res.pid
      var parentPage = this.$parent.PagingObj
      parentPage.pid = pid
      this.$emit('rowClick', parentPage)
    },
    handleTableChange (pagination, filters, sorter) {
      var obj = this.$parent.searchObj
      const pager = { ...this.pagination }
      pager.current = pagination.current
      this.pagination = pager
      console.log(this.pagination)
      obj.page = pager.current
      this.$parent.siteAll(obj)
    },
    // 状态按钮改变事件
    switchBtn (text, record, index) {
      // console.log(event.target.innerText)
      var status = event.target.innerText === '可采集' ? 0 : 1
      this.status = status
      // console.log(text.remark)
      this.statusChange(text.pid, status)
    },
    // 状态按钮改变事件
    statusChange (pid, status) {
      // console.log(pid)
      status = status + '' || null
      // remark = remark || null
      let url = `${this.baseURL}/web_site/update?pid=${pid}&status=${status}`
      this.$http.get(url).then((res) => {
        console.log(res)
      }, (reject) => {
        console.log(reject)
      })
    },
    // 提交备注
    remarkHandler (text) {
      let val = event.target.value
      text.remark = val
      let url = `${this.baseURL}/web_site/update?pid=${text.pid}&remark=${val}`
      this.$http.get(url).then((res) => {
        console.log(res)
      }, (reject) => {
        console.log(reject)
      })
    },
    // 配置信息
    configuration (text) {
      this.conInfo = true
      var jsonParse = JSON.parse(text.rule)
      this.pid = text.pid
      setTimeout(() => {
        if (!text.rule) {
          this.form.setFieldsValue({
            filter_rule: '',
            header: '',
            author: '',
            issueTime: '',
            content: '',
            page_size: ''
          })
        } else {
          this.form.setFieldsValue({
            filter_rule: jsonParse.filter_rule,
            header: jsonParse.header,
            author: jsonParse.author,
            issueTime: jsonParse.issueTime,
            content: jsonParse.content,
            page_size: jsonParse.page_size
          })
        }
      }, 0)
    },
    // 配置信息提交
    conCommit () {
      this.form.validateFields((err, fieldsValue) => {
        if (err) {
          return
        }
        var ruleRep = fieldsValue.filter_rule.replace('+', '@')
        fieldsValue.filter_rule = ruleRep
        const values = encodeURIComponent(JSON.stringify(fieldsValue))
        // console.log(values)
        var url = `${this.baseURL}/web_site/update?pid=${this.pid}&rule=${values}`
        this.$http.get(url).then((res) => {
          this.handleTableChange(this.pagination)
          this.conInfo = false
        })
      })
    },
    // 监测
    monitor (text) {
      console.log(text)
      // var str = encodeURIComponent(text.rule)
      // console.log(str1)
      let url = `${this.baseURL}/web_site/spider?url=${text.address}&rule=${text.rule}`
      this.$http.get(url).then((res) => {
        console.log(res)
      }, (reject) => {
        console.log(reject)
      })
    },
    // 空事件阻止冒泡
    bubbling () {
      // console.log(1111)
    },
    // 子table的模态框
    showModal () {
      this.visible = true
    }
  },
  watch: {
    obj: {
      handler (newV, oldV) {
      },
      deep: true
    },
    switchState: {
      handler () {
        console.log(2222)
      }
    }
  },
  computed: {
    rowSelection () {
      // const selectedRowKeys = this
      return {
        onChange: (selectedRowKeys, selectedRows) => {
          console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows)
        },
        getCheckboxProps: record => ({
          props: {
            // disabled: record.name === 'Disabled User', // Column configuration not to be checked
            // name: record.name
          }
        })
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
</style>



