import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home/home'
// import UrlList from '@/components/UrlManage/UrlList/urlList'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    }
  ]
})



