<template>
    <component :is="comName"></component>
</template>

<script>
import SiteM from './siteM/site'
import UrlList from './UrlList/urlList'
import UrlMonitor from './UrlMonitor/UrlMonitor'
export default {
  name: 'UrlManage',
  props: ['comName'],
  components: {
    SiteM,
    UrlList,
    UrlMonitor
  },
  data () {
    return {
    }
  }
}
</script>

<style lang="stylus" scoped>
</style>



